* {
  margin: 0;
  padding: 0;
}

body{
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
}

.header{
  display: flex;
  align-items: center;
  height: 70px;
  padding: 0 30px;
  box-shadow: 0 3px 5px rgba(57, 63, 72, 0.3);
}
.header > .header-logo{
  width: 100px;
}
.landing{
  padding: 40px 30px;
  height: calc(100vh - 150px);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.landing .landing-image-grid{
  max-width: 50%;
}
.landing .landing-infos{
  margin-top: 40px;
  flex: 1;
}
.landing .landing-infos .landing-title{
  font-size: 60px;
  margin-bottom: 20px;
}
.landing .landing-infos .landing-paragraph{
  font-size: 23px;
  width: 50%;
  line-height: 1.6;
}
.main{
  padding: 40px 0;
}
.main .cards{
  display: flex;
  justify-content: center;
  gap: 10px;
  flex-wrap: nowrap;
  overflow: hidden;
}
.main .cards .card{
  width: 300px;
  font-size: 12px;
  flex: 0 0 auto; 
  display: flex;         
  flex-direction: column; 
}
.main .cards .card .card-header{
  position: relative;
}
.main .cards .card .card-photo{
  width: 100%;
  border-radius: 6px;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  -ms-border-radius: 6px;
  -o-border-radius: 6px;
}
.main .cards .card .header-mark{
  position: absolute;
  left: 10px;
  top: 10px;
  background-color: white;
  padding: 10px 0;
  width: 80px;
  font-weight: bold;
  font-size: 12px;
  text-align: center;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
}
.main .cards .card .rate{
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 14px;
  margin: 10px 0;
}
.main .cards .card .rate .rate-number{
  font-weight: 400;
}
.main .cards .card .rate .country{
  color: #918E9B;
}
.main .cards .card .info{
  font-size: 15px;
}
.main .cards .card .info-title{
  margin-bottom: 5px;
}
.main .cards .card .parice-num{
  font-weight: bold;
}